<template>
  <div class="fill-height">
    <app-bar title="Palavra do presidente">
      <template v-slot:append>
        <v-btn icon to="/" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <async-container :loading="!loaded">
      <v-card
        max-width="680"
        class="mx-auto mb-7"
        :dark="$store.state.dark"
      >
        <v-card-text>
          <div class="text-center">
            <v-avatar v-if="foto" size="7rem" class="elevation-2 mb-1">
              <img :src="foto" alt=""/>
            </v-avatar>
            <p class="title mb-0" style="line-height: 1rem">{{nome}}</p>
            <p class="blue--text subtitle-2 mb-0">Presidente do CRECI-{{uf}}</p>
          </div>
          <p class="text-justify" style="white-space: pre-line">{{texto}}</p>
        </v-card-text>
      </v-card>
    </async-container>
  </div>
</template>

<script>
  import AppBar from '@/components/AppBar';
  import AsyncContainer from '@/components/AsyncContainer';
  import AppWebClient from '@/http/AppWebClient';
  import ImageHelper from '@/helpers/ImageHelper';
  import CreciHelper from '@/helpers/CreciHelper';

  export default {
    name: 'PalavraPresidenteCreci',
    components: {AppBar, AsyncContainer},
    data: () => ({
      loaded: false,
      texto: '',
      nome: '',
      foto: null,
      uf: '',
    }),
    methods: {
      async loadData() {
        try {
          const regional = this.$store.getters['session/payload'].regional;
          this.uf = CreciHelper.idParaUf(regional);
          const appWebClient = new AppWebClient(true);
          const data = await appWebClient.getPresidente(regional);
          this.texto = data.texto;
          this.nome = data.nome;
          const blob = await appWebClient.getPresidenteFoto(regional);
          this.foto = await ImageHelper.blobToDataURI_async(blob);
        } catch (e) {
          // eslint-disable-next-line no-console
          console.log(e);
          let dados = localStorage.getItem('presidentecreci');
          if (dados) {
            dados = JSON.parse(dados);
            this.texto = dados.texto;
            this.nome = dados.nome;
            this.foto = dados.foto;
            this.uf = dados.uf;
          }
          else await this.$router.push('/');
        } finally {
          this.loaded = true;
        }
      },
    },
    created() {
      this.loadData();
    },
    beforeDestroy() {
      if (this.loaded) {
        localStorage.setItem('presidentecreci', JSON.stringify({
          texto: this.texto,
          nome: this.nome,
          foto: this.foto,
          uf: this.uf,
        }));
      }
    },
  }
</script>
